@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cardo&family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Londrina+Outline&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: 'Cardo', serif;
  background-color: rgb(30, 30, 30);
}

@media (prefers-reduced-motion: no-preference) {
  .react-logo {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
  }

  .mui-logo, .xavy-logo {
    -webkit-animation: logo-flip infinite 5s linear;
            animation: logo-flip infinite 5s linear;
  }

  .footer-marquee {
    -webkit-animation: marquee infinite 60s linear;
            animation: marquee infinite 60s linear;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes logo-flip {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1)
  }
  50% {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1)
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1)
  }
}

@keyframes logo-flip {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1)
  }
  50% {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1)
  }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1)
  }
}

@-webkit-keyframes marquee {
  0% {-webkit-transform: translateX(100%);transform: translateX(100%);}
 100% {-webkit-transform: translateX(-50%);transform: translateX(-50%);} 
}

@keyframes marquee {
  0% {-webkit-transform: translateX(100%);transform: translateX(100%);}
 100% {-webkit-transform: translateX(-50%);transform: translateX(-50%);} 
}
